.pagination-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 18px;
  margin: 40px 0 32px 0;
  background: transparent;
}
.pagination-btn {
  background: #f9f1e7;
  border: none;
  border-radius: 8px;
  padding: 12px 24px;
  font-size: 1.1em;
  color: #b88e2f;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}
.pagination-btn.active {
  background: #b88e2f;
  color: #fff;
  font-weight: bold;
} 