.filter-bar-strict {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #f9f6f1;
  padding: 20px 32px;
  font-size: 1.1rem;
  border-radius: 0 0 0 0;
}
.filter-bar-left {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1 1 auto;
}
.filter-icon {
  font-size: 1.35em;
  margin-right: 4px;
  color: #222;
  display: flex;
  align-items: center;
}
.filter-label {
  font-weight: 500;
  margin-right: 18px;
  color: #222;
}
.filter-view-icons {
  display: flex;
  gap: 8px;
  font-size: 1.25em;
  margin-right: 18px;
  color: #222;
}
.filter-grid-icon, .filter-list-icon {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.filter-divider {
  display: inline-block;
  width: 1px;
  height: 28px;
  background: #d8d8d8;
  margin: 0 24px 0 16px;
}
.filter-results {
  color: #222;
  font-size: 1.05em;
  font-weight: 400;
  margin-right: 32px;
}
.filter-bar-right {
  display: flex;
  align-items: center;
  gap: 18px;
  margin-left: 20rem;
}
.filter-bar-right span {
  color: #222;
  font-weight: 500;
  font-size: 1.08em;
}
.filter-show-input {
  width: 40px;
  text-align: center;
  background: #fff;
  border: none;
  border-radius: 4px;
  font-weight: bold;
  margin: 0 8px;
  padding: 4px 0;
  color: #888480;
  font-size: 1em;
  box-shadow: 0 0 0 1px #f5f5f5;
}
.filter-sort-select {
  background: #fff;
  border: none;
  border-radius: 4px;
  padding: 6px 12px;
  font-size: 1em;
  color: #8d8282;
  font-weight: 500;
  min-width: 110px;
}