.shop-header {
  position: relative;
}
.shop-header-navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 32px;
  background: #fff;
  border-radius: 16px 16px 0 0;
  min-height: 48px;
}
.shop-header-logo {
  display: flex;
  align-items: center;
  font-size: 1.7rem;
  font-weight: bold;
  gap: 1px;
}
.logo-text {
  color: #222;
  font-size: 1.5rem;
  font-weight: bold;
  letter-spacing: 1px;
}
.shop-header-nav {
  display: flex;
  gap: 40px;
}
.shop-header-nav a {
  color: #222;
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
  transition: color 0.2s;
}
.shop-header-nav a:hover {
  color: #B88E2F;
}
.shop-header-icons {
  display: flex;
  gap: 24px;
  font-size: 1.3rem;
}
.shop-header-bg {
  position: relative;
  height: 180px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  overflow: hidden;
}
.shop-header-bg::before {
  content: '';
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  background: url('../images/shop.jpg') center/cover no-repeat;
  filter: blur(2px);
  z-index: 1;
}
.shop-header-content {
  position: relative;
  
  padding: 24px 48px 48px 48px;
  border-radius: 0 0 16px 16px;
  text-align: center;
  z-index: 2;
}
.shop-header-content h2 {
  margin: 0;
  font-size: 2.5rem;
}
.shop-breadcrumb {
  font-size: 1rem;
  color: #080808;
} 