.feature-bar {
  display: flex;
  justify-content: space-around;
  background: #FFF7E3;
  padding: 32px 0;
  border-radius: 0;
  margin-top: 32px;
  font-size: 1.1em;
  gap: 0;
}
.feature-bar div {
  text-align: center;
}
.feature-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 6px;
}
.feature-title {
  font-weight: light-bold;
  font-size: 1.15em;
  margin-bottom: 2px;
  color: #222;
}
.feature-desc {
  color: #888;
  font-size: 1em;
}

.footer-bar {
  background: #fff;
  padding: 48px 0 0 0;
  margin-top: 0;
  border-radius: 0 0 12px 12px;
  font-size: 1.1em;
  border-top: 1px solid #eee;
}
.footer-main {
  display: flex;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 32px 32px 32px;
  gap: 32px;
}
.footer-col {
  flex: 1 1 0;
  min-width: 180px;
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.footer-brand {
  flex: 2 1 0;
  min-width: 260px;
}
.footer-logo {
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 12px;
  color: #222;
}
.footer-address {
  color: #888;
  font-size: 1em;
  margin-bottom: 0;
}
.footer-col-title {
  font-weight: bold;
  color: #898989;
  font-size: 1em;
  margin-bottom: 8px;
}
.footer-col a {
  color: #222;
  text-decoration: none;
  font-weight: 500;
  margin-bottom: 2px;
  transition: color 0.2s;
}
.footer-col a:hover {
  color: #b88e2f;
}
.footer-newsletter-form {
  display: flex;
  align-items: center;
  gap: 0;
  border-bottom: 1px solid #d8d8d8;
  padding-bottom: 2px;
  margin-top: 8px;
}
.footer-newsletter-form input[type="email"] {
  border: none;
  outline: none;
  font-size: 1em;
  color: #888;
  background: transparent;
  padding: 4px 0;
  flex: 1 1 auto;
}
.footer-newsletter-form button {
  background: none;
  border: none;
  color: #222;
  font-weight: bold;
  font-size: 1em;
  cursor: pointer;
  margin-left: 8px;
  padding: 0;
  letter-spacing: 1px;
  transition: color 0.2s;
}
.footer-newsletter-form button:hover {
  color: #b88e2f;
}
.footer-bottom {
  text-align: left;
  color: #898989;
  font-size: 0.98em;
  border-top: 1px solid #eee;
  padding: 18px 32px 18px 32px;
  margin-top: 0;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
} 